import React from 'react';
import api from './services/api';
import './App.css';
import cursor from './services/cursor.png';
import press from './services/press.png';
import circulo from './services/circulo.png';
import $ from "jquery";

$(document).mousemove(function (e) {
  $(".pointer").css({ left: e.pageX, top: e.pageY });
});

export default class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      registro: " ",
      // 28560 vangogh
      // 111628 hopper 27992
      // 24645 great wave img https://www.artic.edu/iiif/2/b3974542-b9b4-7568-fc4b-966738f61d78/full/843,/0/default.jpg
      title: "The Great Wave",
      image_id: 24645,
      artist_title: "Katsushika Hokusai",
      date_display: "1826â€“1836",
      urlImagem: "https://www.artic.edu/iiif/2/b3974542-b9b4-7568-fc4b-966738f61d78/full/843,/0/default.jpg",
      medium_display: " ",
      pagina: " ",
      pagina_anterior: " ",
      max: 115507,
      min: 1,
      press: $("press"),
    };
    this.buscar = this.buscar.bind(this);
  }
  //https://api.artic.edu/api/v1/artworks?fields=id,title,artist_title,date_display,image_id
  //https://api.artic.edu/api/v1/artworks?page=1&limit=1&fields=id,title,artist_title,date_display,image_id

  async buscar() {
    const registro = "?page=" + this.state.pagina + "&limit=1&fields=id,title,artist_title,date_display,image_id";
    const resp = await api.get(registro);
    this.setState({
      registro: resp.data.data[0].id,
      title: resp.data.data[0].title,
      image_id: resp.data.data[0].image_id,
      date_display: resp.data.data[0].date_display,
      artist_title: resp.data.data[0].artist_title,
      medium_display: resp.data.data[0].medium_display,
    });

    this.setState({ urlImagem: "https://www.artic.edu/iiif/2/" + this.state.image_id + "/full/843,/0/default.jpg" });
    console.log(resp.data.data[0]);
  }

  idShuffle = () => {
    
    this.setState({ pagina: Math.floor(Math.random() * (6850 - 1))});
    this.setState({ pagina_anterior: this.state.pagina});
    this.buscar();

    console.log ("pagina nova " + this.state.pagina);
    console.log ("pagina anterior " + this.state.pagina_anterior);
  }  
  
  render() {
    return (
      <div className="App" key="registro" onClick={this.idShuffle}>
        
        <p>{this.state.artist_title}. <b>{this.state.title}</b>, {this.state.medium_display}. <small>{this.state.date_display}</small></p>
        <figure id="imgcontainer">
            <img id="principal" src={this.state.urlImagem} alt="artwork image not avaliable, please shuffle again." draggable="false"></img>     
        </figure>
          <img id="circulo1" className="circulo rodar" src={circulo} draggable="false"></img>
          <img id="circulo2" className="circulo rodar" src={circulo} draggable="false"></img>

        <div className="pointer">
          <img id="shuffle" src={cursor} draggable="false"></img>
          <img id="press" src={press} draggable="false"></img>
        </div>
      </div>
    )
  }
}
